// 歌单相关API
import request from "@/utils/request";

// 获取每日推荐歌单列表
export const apiGetRecommend = (limit)=>request.get(`/personalized?limit=${limit}`)

// 通过歌单id获取歌曲列表
export const apiGetMusicInMusicListByID = (id,limit=1000, offset=0)=>request(`/playlist/track/all?id=${id}&limit=${limit}&offset=${offset}`)

// 通过歌单id获取歌单详情
export const apiGetMusicDetailById = (id)=>request(`/playlist/detail?id=${id}`)
