export default [
    {
        path:'/',
        redirect:'/mainpage'
    },
    {
        name:'MainPage',
        path:'/mainpage',
        component:()=>import('@/Pages/MainPage'),
        redirect: '/mainpage/recommend',
        children:[
            {
                name:'Recommend',
                path:'recommend',
                component:()=>import('@/Pages/MainPage/Recommend')
            },
            {
                name:'MusicRoom',
                path:'musicroom',
                component:()=>import('@/Pages/MainPage/MusicRoom')
            },
            {
                name:'Podcast',
                path:'podcast',
                component:()=>import('@/Pages/MainPage/Podcast')
            },
            {
                name:'FM',
                path:'fm',
                component:()=>import('@/Pages/MainPage/FM')
            }
        ]
    },
    {
        name:'Live',
        path:'/live',
        component:()=>import('@/Pages/Live')
    },
    {
        name:'Mv',
        path:'/mv',
        component:()=>import('@/Pages/Mv')
    },
    {
        name:'Community',
        path:'/community',
        component:()=>import('@/Pages/Community')
    },
    {
        name:'Main',
        path:'/main',
        component:()=>import('@/Pages/Main')
    }
]
