<template>
  <div class="bofangqi" :style="{backgroundColor: randomColor}">
    <audio id="audio" preload="metadata" ref="audioRef" loop>
      <source  type="audio/mpeg">
    </audio>
<!--    圈圈-->
    <div class="circle" >
<!--    开始播放-->
        <div class="musicStart musicFlag" @click="startAndEndMusic('start')" v-show="startAndEndFlag==='start'">&#xea81;</div>
<!--    结束播放-->
        <div class="musicEnd musicFlag" @click="startAndEndMusic('end')"  v-show="startAndEndFlag==='end'">&#xe612;</div>
    </div>

    <!--      音乐图片-->
    <div class="musicImg">
      <img :src="musicInfo.picUrl" alt="al">
    </div>
<!--    播放列表-->
    <div class="musicList">
<!--      图标-->
      <i>&#xe6a5;</i>
    </div>
<!--    跑马灯效果的文字-->
    <div class="title">
      <transition name="title" appear>
        <p id="title" v-show="flag">{{ musicInfo.name }}</p>
      </transition>
    </div>
  </div>
</template>

<script>
/**
 * 播放触发自定义事件 playMusic
 * 切换歌曲触发自定义事件 clear
 * 触发播放需要传递一个对象keys为 url，name，picUrl
 * */

// 随机颜色
import {randomColor} from "@/utils/randomColor";

export default {
  name: "MyAudio",
  data(){
    return {
      startAndEndFlag: 'end', // 控制播放暂停
      flag: true, // 控制音乐名称和歌手名称显示或者隐藏

      musicInfo:{}, // 歌曲名称和歌手名称

      timer: null, // 跑马灯计时器
      autoplayTimer: null, // 自动播放定时器

      randomColor: '', // 随机颜色
    }
  },
  methods:{
    // 初始化音乐播放器
    initMusicPlay(res){
      this.randomColor = randomColor(.9)
      this.musicInfo = res
      this.$nextTick(()=>{
        this.$refs.audioRef.src = res.url
      })
    },

    // 格式化播放器数据
    clearData(){
      Object.assign(this._data, this.$options.data())
      clearInterval(this.timer)
      clearTimeout(this.autoplayTimer)
    },

    // 开始或者结束播放音乐
    startAndEndMusic(flag){
      let audio = document.getElementById('audio')
      // 播放
      if(flag === 'end') {
        this.startAndEndFlag = 'start'
        audio.play() // 播放音频
        // 开启计时器
        this.move()
      }else {
        this.startAndEndFlag = 'end'
        audio.pause() // 结束播放
        // 清除计时器
        clearInterval(this.timer)
        clearTimeout(this.autoplayTimer)
        this.timer = null
        this.autoplayTimer = null
        this.flag = true
      }
    },

    // 自动播放音乐
    autoplayMusic() {
      this.autoplayTimer = setTimeout(()=>{
        this.startAndEndMusic('end')
      },500)
    },

    // 文字跑马灯事件
    move(){
      if(this.timer !== null) return
      this.timer = setInterval(()=>{
        this.flag = !this.flag
      },5000)
    }
  },
  mounted() {
    // 自定义事件接收mainBox的数据
    this.$BUS.$on('playMusic',(res)=>{
      this.initMusicPlay(res)
    })
    // 自定义事件销毁播放器
    this.$BUS.$on('clear',this.clearData)
    // 自定义事件自动播放音乐
    this.$BUS.$on('play', this.autoplayMusic)
  }
}
</script>

<style scoped lang="less">
// 播放器
.bofangqi {
  position: fixed;
  bottom: 7vh;
  left: 1.2vh;
  width: 90%;
  border-bottom-right-radius: 3vh;
  border-top-right-radius: 3vh;
  height: 9vh;
  z-index: 999;
}
// 播放暂停的小圆圈
.circle {
  position: absolute;
  width: 5vh;
  height: 5vh;
  top: 50%;
  right: 15%;
  transform: translateY(-50%);
  border: 3px solid #ffffff;
  border-radius: 5vh;
  .musicFlag {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 18px;
    font-family: iconfont;
    transform: translate(-50%,-50%);
    color: white;
    margin-left: 1px;
  }
  .musicEnd {
    font-size: 18px;
  }
  .musicStart {
    left: 48% !important;
  }
}

// 音乐图片
.musicImg {
  width: 10vh;
  height: 10vh;
  position: absolute;
  top: -1vh;
  left: 0;
  img{
    width: 100%;
    height: 100%;
    border-radius: 10%;
  }
}
// 播放列表
.musicList {
  position: absolute;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
  font: 700 18px iconfont;
  color: #ffffff;
}
// 歌曲名和歌曲作者
.title {
  position: absolute;
  top: 50%;
  left: 11vh;
  transform: translateY(-50%);
  width: 50%;
  height: 5vh;
  line-height: 5vh;
  overflow: hidden;
  p {
    color: white;
    font-size: 14px;
    text-align: left;
  }
}

// 跑马灯进入和离开动画
.title-enter-active {
  animation: horseLampEnter 5s linear;
}

.title-leave-active {
  animation: horseLampLeave 5s linear;

}
</style>
