// 搜索接口
import request from "@/utils/request";

// 获取热门搜索（简易）
export const apiGetHotSearch = ()=>request('/search/hot')

// 获取默认搜索关键词
export const apiGetSearchDefKeyWord = ()=>request('/search/default')

// 获取搜索推荐
export const apiGetSearchRecommend = keyword=>request(`/search/suggest?keywords=${keyword}&type=mobile`)

// 通过关键字搜索
export const apiSearchByKeyword = obj=>request(`/cloudsearch?keywords=${obj.keyword}&type=${obj.type}&limit=${obj.limit}&offset=${obj.offset}`)
