import {apiGetBannerList} from '@/api/baseList'
import {apiGetRecommend,apiGetMusicInMusicListByID} from "@/api/musicList";

export default {
    namespaced: true,
    actions:{
        // 轮播图
        async getBannerList({commit}) {
            try {
                let res = await apiGetBannerList()
                if(res.code== 200) {
                    commit('SET_BANNER',res.banners)
                }
            }catch (e) {
                console.log(e)
            }
        },

        // 获取每日推荐30首歌单
        async getRecommend({commit}) {
            try {
                let res = await apiGetRecommend(1)
                if(res.code === 200) {
                    commit('SET_RECOMMEND',res.result[0])
                }
            }catch (e) {
                console.log(e)
            }
        },

        // 获取宝藏歌单列表
        async getMoneyMusicList({commit}) {
            try {
                let res = await apiGetRecommend(12)
                if(res.code!== 200) return
                commit('SET_MONEYMUSICLIST', res.result)
            }catch (e) {
                console.log(e)
            }
        },

        // 获取推荐歌曲1列表
        async getRecommendMusic1({commit}) {
            try {
                let res = await apiGetMusicInMusicListByID(5444463825,18)
                commit('SET_RECOMMENDMUSCI1', res.songs)
            }catch (e) {
                console.log(e)
            }
        }
    },
    state:{
        bannerListData:[],  // 轮播图数据
        recommendData:{}, // 每日推荐歌单
        moneyMusicList:[], // 宝藏歌单推荐
        recommendMusic1:[], // 推荐歌曲
        recommendMusic2:[]
    },
    mutations:{
        'SET_BANNER'(state,val) {
            state.bannerListData = val
        },
        'SET_RECOMMEND'(state, val) {
            state.recommendData = val
        },
        'SET_MONEYMUSICLIST'(state,val) {
            state.moneyMusicList = val
        },
        'SET_RECOMMENDMUSCI1'(state, val) {
            state.recommendMusic1 = val
        }
    },
    getters:{}
}
